@import url(https://netdna.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css);
body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:#333}.container{max-width:800px;margin:0 auto}

.header-top{display:grid;grid-template-columns:2fr 1fr;min-height:180px}.header-top button:focus,.header-top button:active{outline:none}.header-top .header-left{margin:auto 0;text-align:left}.header-top .header-left .box{width:130px;height:30px;color:#007e90;background-color:#fff;border:1px solid #007e90;transition:all 250ms ease;will-change:transition;display:inline-block;text-align:center;cursor:pointer;position:relative;font-weight:600}.header-top .header-left .box:active{transform:translateY(5px)}.header-top .header-left .sort-button:nth-child(2){border-left:none;border-right:none}.header-top .header-left .sort-reverse{margin-top:10px}.header-top .header-left .direction-button{cursor:pointer}.header-top .header-left .direction-button:disabled{cursor:default;color:#7c7c7c;background-color:#fff;border-color:#7c7c7c}.header-top .header-left .direction-button:nth-child(2){border-left:none}.header-top .header-left .checked{background-color:#007e90;color:#fff}.header-top .header-right{margin:auto 0;text-align:right}.header-top .header-right .nav .checked .box{background-color:#007e90}.header-top .header-right .nav .checked .box span{color:#fff;transform:translateY(30px)}.header-top .header-right .nav .checked .box span:before{transform:translateY(0px);opacity:1}.header-top .header-right .nav .box{width:110px;height:100px;background-color:#fff;border:1px solid #007e90;transition:all 250ms ease;will-change:transition;display:inline-block;text-align:center;cursor:pointer;position:relative;font-weight:600}.header-top .header-right .nav .box:active{transform:translateY(5px)}.header-top .header-right .nav .box span{position:absolute;transform:translate(0, 20px);left:0;right:0;transition:all 300ms ease;font-size:1.2em;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;color:#007e90}.header-top .header-right .nav .box span:before{font-size:1em;font-family:FontAwesome;display:block;transform:translateY(-80px);opacity:0;transition:all 300ms ease-in-out;font-weight:normal;color:#fff}.header-top .header-right .nav .nav-item-table span:before{content:'\f0c9'}.header-top .header-right .nav .nav-item-preview span:before{content:'\f0ce'}.lang-item .lang-btn{cursor:pointer;position:relative;width:70px;height:30px;margin-top:5px;margin-bottom:10px;background-color:#fff;border:1px solid #007e90}.lang-item .knobs:before{content:'RU';position:absolute;top:-1px;left:-1px;width:20px;height:10px;color:#fff;font-size:10px;font-weight:bold;text-align:center;line-height:1;padding:9px 4px;background-color:#007e90;border:1px solid #007e90;border-radius:2px;transition:0.3s ease all,left 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15)}.lang-item .lang-btn:active>.knobs:before{width:46px}.lang-item .lang-btn.checked:active>.knobs:before{margin-left:-26px}.lang-item .lang-btn.checked>.knobs:before{content:'EN';left:39px;color:#007e90;border:1px solid #007e90;background-color:#fff}.lang-item .lang-btn.checked ~ .layer{background-color:#fcebeb}

.filter{margin-top:10px}.filter-input{width:130px;box-sizing:border-box;border:1px solid #007e90;font-size:16px;padding:8px 10px 8px 10px}.filter-input:focus{outline:none}.filter-clear{cursor:pointer;color:#007e90;margin-left:-20px;opacity:0;visibility:hidden;transition:all 0.2s ease-in}.filter-clear-active{opacity:1;visibility:visible}

.addUserForm{display:flex;justify-content:flex-start;margin:0 auto 10px;width:100%}.addUserForm .col{max-width:100px;width:100%;margin-right:10px;box-sizing:border-box}

@-webkit-keyframes innerLoader{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}@keyframes innerLoader{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}.innerLoader div{box-sizing:border-box !important}.innerLoader>div{position:absolute;width:50px;height:50px;top:25px;left:25px;border-radius:50%;border:8px solid #000;border-color:#007e90 transparent #007e90 transparent;-webkit-animation:innerLoader 1s linear infinite;animation:innerLoader 1s linear infinite}.innerLoader>div:nth-child(2){border-color:transparent}.innerLoader>div:nth-child(2) div{position:absolute;width:100%;height:100%;transform:rotate(45deg)}.innerLoader>div:nth-child(2) div:before,.innerLoader>div:nth-child(2) div:after{content:"";display:block;position:absolute;width:8px;height:8px;top:-8px;left:13px;background:#007e90;border-radius:50%;box-shadow:0 42px 0 0 #007e90}.innerLoader>div:nth-child(2) div:after{left:-8px;top:13px;box-shadow:42px 0 0 0 #007e90}.Loader{width:99px;height:99px;display:inline-block;overflow:hidden;background:none}.innerLoader{width:100%;height:100%;position:relative;transform:translateZ(0) scale(0.99);-webkit-backface-visibility:hidden;backface-visibility:hidden;transform-origin:0 0}.innerLoader div{box-sizing:content-box}.center{text-align:center}

.users-table{box-shadow:0 10px 5px rgba(163,163,163,0.6)}.users-table .users-table-item{display:flex;flex-direction:row;justify-content:space-between;align-items:center;padding:10px;border:1px solid rgba(163,163,163,0.404)}.users-table .users-table-item .table-item-id{width:30px;height:30px;font-size:13px;color:#007e90;border:2px solid #007e90;border-radius:50%;text-align:center;line-height:30px;margin-left:5px}.users-table .users-table-item img{width:50px;height:50px}.users-table .users-table-item .table-item-icon,.users-table .users-table-item .table-item-name,.users-table .users-table-item .table-item-phone,.users-table .users-table-item .table-item-fav{flex:1 1}.users-table .users-table-item .table-item-age{flex:1 1;text-align:center}.users-table .users-table-item .table-item-fav{text-align:right;margin-right:10px;cursor:pointer}.users-table .users-table-item .table-item-fav i{color:#007e90}

.videoContent{grid-column:1 / 3;display:grid;grid-template-columns:1fr 1fr}.videoContent .preview-item-phrase{padding-right:20px}.videoContent .preview-item-fav{padding-right:30px}.users-preview{display:grid;grid-template-columns:1fr 1fr;grid-gap:20px}.users-preview .users-preview-item{min-height:240px;padding:20px;border:1px solid rgba(163,163,163,0.404);box-shadow:0 5px 5px rgba(163,163,163,0.4)}.users-preview .users-preview-item .preview-item-row{display:flex;flex-direction:row;justify-content:space-between;text-align:center;margin-top:10px}.users-preview .users-preview-item .preview-item-row img{width:50px;height:50px}.users-preview .users-preview-item .preview-item-row .preview-item-name{margin-left:20px}.users-preview .users-preview-item .preview-item-row .preview-item-fav{flex:3 1;text-align:right;margin:auto 0;cursor:pointer}.users-preview .users-preview-item .preview-item-row .preview-item-fav i{color:#007e90}.users-preview .users-preview-item .preview-item-age{margin-top:30px;margin-left:6px;margin-bottom:20px}.users-preview .users-preview-item .preview-item-phone,.users-preview .users-preview-item .preview-item-phrase{margin-left:6px}.users-preview .users-preview-item .preview-item-phone{margin-bottom:20px}.users-preview .users-preview-item .preview-item-phone i{margin-right:5px}@media only screen and (max-width: 500px){.videoContent{grid-column:1 / 2;grid-template-columns:1fr}.users-preview{grid-column:1 / 2;grid-template-columns:1fr}}

